import React, { useState } from 'react';
import {
  AppstoreOutlined,
  SettingFilled,
  MessageOutlined,
  DatabaseFilled,
  MonitorOutlined,
  
} from '@ant-design/icons';
import type { MenuProps } from 'antd';
import { Button, Menu } from 'antd';
import styles from './HomeMenu.module.css'
type MenuItem = Required<MenuProps>['items'][number];

function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[],
  type?: 'group',
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
    type,
  } as MenuItem;
}

const items: MenuItem[] = [
  getItem('', '1', <MonitorOutlined style={{fontSize:20}}/>),
  getItem('', '2', <AppstoreOutlined style={{fontSize:20}}/>),
  getItem('', '3', <DatabaseFilled style={{fontSize:20}}/>),

  getItem('', 'sub1', <MessageOutlined style={{fontSize:20}}/>, [
    getItem('', '5'),
    getItem('', '6'),
    getItem('', '7'),
  ]),

  getItem('', '8', <SettingFilled style={{fontSize:20}}/>),
];


function HomeMenu({collapsed}:{collapsed:boolean}){
  
  // 1/3 1/3
  function clickItem(e){
    console.log("clickItem===", e.key);
  }

  return (
    <div className={ collapsed ? styles.menuCollapsed:styles.menu}>
      <Menu style={{fontSize:16}}
        defaultSelectedKeys={['1']}
        inlineCollapsed={collapsed}
        items={items}
        mode="inline"
        onClick={clickItem}
      />
    </div>
  );
}


export default HomeMenu;

